@import '../../style/main.scss';

.course {
  background-color: var(--tertiary);
  height: auto;
  &_container {
    @include flex(column);
    gap: 100px;
    margin: 0 auto;
    height: 100%;
    .wrapper {
      @include flex;
      width: 100%;
      height: 100%;
      .skills {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: auto;
        justify-content: center;
        row-gap: 80px;
        column-gap: 50%;
        @media (max-width: 575.98px) {
          grid-template-columns: repeat(2, 1fr);
        }
        &_container {
          @include flex(column);
          gap: 25px;
          &_icon {
            width: 56px;
            height: 56px;
          }
        }
      }
    }
  }
}
